@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300,400,600,700,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Source Sans Pro", sans-serif;
}

.site-name h3 {
  font-size: 5em;
  margin: 0;
}

.site-desc {
  text-align: right;
  width: 50%;
  margin-left: auto;
  margin-right: 0;
  font-size: 1.8em; }

.profilepic {
  margin-bottom: 4em;
  margin-left: 11em;
  position: relative; }
  .profilepic img {
    height: 350px;
    object-fit: cover;
    width: 300px; }

.ppbg {
  width: 350px;
  height: 350px;
  background-color: #ffd7cd;
  position: absolute;
  z-index: -1;
  left: 2%;
  top: 12%; }

.specializein {
  text-align: center;
  font-size: 1.8em;
  font-weight: 300;
  margin: 4em 0; }

.skillset p {
  font-size: 2em; }

.skillset ul {
  font-size: 1.8em;
  list-style-type: none;
  padding: 0; }

.skillset li {
  opacity: 0.1;
  font-size: 1.3em;
  font-weight: bold; }

.right-align {
  text-align: right;
  width: 50%;
  margin-left: auto;
  margin-right: 0; }
  .right-align.gallery-shot {
    width: 80%; }

.experience ul {
  width: 50%;
  list-style-type: none; }
  .experience ul li {
    margin: 2em 0; }

.maintitle {
  font-size: 3em; }

.random-shot li {
  width: 23.5%;
  display: inline-block;
  margin: 1em 0.5em; }
  .random-shot li img {
    width: 100%; }

.copyright {
  text-align: center; }

.layer {
  margin-bottom: 5em; }

.draggable {
  height: 100px;
  width: 100px;
  background: blue;
  border-radius: 10px;
  margin: 50px;
  color: white;
  font-size: 20px;
  padding: 10px; }

@media screen and (max-width: 780px) {
  .App {
    padding: 1em; }
  .ppbg {
    z-index: -1;
    left: 0; }
  .profilepic {
    margin-left: 0; }
  .site-desc {
    width: 80%; }
  .experience ul {
    width: 80%; } }

